@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
ul {
	margin: 0;
	margin-left: 1rem;
	padding: 0px 20px 5px 20px;
}
.side-section {
	background-color: #3f4d67;
	height: 100vh;
	overflow: hidden;
	width: 21vw;
	/*136.6 total width*/
	box-shadow: 1px 0 20px 0 #3f4d67;
	color: #a9b7d0;
	transition: all 0.3s ease-in-out;
	z-index: 2;
}

.side-section:hover {
	transition: all 0.3s ease-in-out;
	overflow-y: scroll;
}

.side-head {
	margin: 1rem;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
}

.nav-item2 {
	font-size: 1.4rem;
	line-height: 2.1rem;
	transition: all 0.3s ease-in-out;
	box-shadow: 1px 0 20px 0 #3f4d67;
	text-align: left;
	border-radius: 0;
	margin-top: 3rem;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: left;
	        align-items: left;
	margin-bottom: 2rem;
	cursor: pointer;
}

.nav-item2:hover,.nav-item2:active  {
	color: #1dc4e9;
}



.nav-item1 {
	font-size: 1rem;
	font-weight: 600;
	color: #e8edf7;
	text-transform: uppercase;
	padding: 25px 20px 5px;
	padding-left: 0;
	margin-bottom: 0;
}

.span-logo {
	margin-right: 1rem;
}

.side-nav {
	margin-top: 0;
}

.toggler {
	font-size: 1rem;
}


.coin-names {
	padding: 7px 7px 7px 60px;
	margin: 0;
	font-weight: 0;
	font-size: 1.4rem;
	background: #39465e;
	width: 100%;
}

.coin-names1 {
	margin: 0;
	padding: 0;
}

.side-nav::after {
	content: "";
	background-color: #1dc4e9;
	z-index: 1027;
	position: absolute;
	left: 0;
	top: 0px;
	width: 3px;
	height: 100vh;
}

.coin-name:hover, .arrow:hover {
	color: #1dc4e9;
	cursor: pointer;
}



.add-line {
	font-size: 1.4rem;
	line-height: 2.1rem;
	transition: all 0.3s ease-in-out;
	box-shadow: 1px 0 20px 0 #3f4d67;
	text-align: left;
	border-radius: 0;
	margin-top: 3rem;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: left;
	        align-items: left;
	margin-bottom: 2rem;
}

.hide{
	display: none;
}

.arrow{
	position: relative;
	left: 44px;
	top:3.5px;
}

.cards{
	display: -webkit-flex;
	display: flex;	
	-webkit-justify-content: space-around;	
	        justify-content: space-around;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	transition: all .5s ease-in-out;
}


.card{
	border-radius: 0;
    box-shadow: 0 1px 20px 0 rgb(69 90 100 / 8%);
    border: none;
    transition: all .5s ease-in-out;
    background: #FFFFFF;
    /*width: 32.1rem;
    height: 16.8rem;*/
  	padding: 30px 25px;
  	min-width: 19vw;
  	/*margin-right: 2rem;*/
  	margin: 1rem;
}

.coin-name{
	font-size: 1.4rem;
	font-weight: 400;
	margin-bottom: 1.5rem!important
}

.price-no{
	font-size: 2.6rem;
	font-weight: 300;
}

.progress-bar{
	box-shadow: 0 10px 20px 0 rgb(0 0 0 / 30%);
	margin-top: 3rem;
}

.price-icon{
	position: relative;
	top: 5px;
}

.loading-animation{
	-webkit-align-self: center;
	        align-self: center;
	position: relative;
    top: 16rem;
}
.search-input {
    border: none;
    width: 18rem;
    height: 4rem;
    font-size: 1.4rem;
    /*border-radius: 13rem;*/
    background: #ffffff;
    outline-style: none;
    box-shadow: 0 6px 12px -5px rgb(65 76 78 / 8%);
    padding-left: 4rem;
}

.input-group{
    display: -webkit-flex;
    display: flex;
    margin: 2rem;
    margin-left: 0rem;
    float: right;
}

.search-icon{
    height: 4rem;
    width: 2rem;
}



.app-div{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: nowrap;
	        flex-wrap: nowrap;
}

.main-ui{
	margin-left: 1rem;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	padding: 3rem;
	width: 79vw;
	/*width: calc(100%-26.4rem)%;*/
}
html {
	font-size: 62.5%;
	/*1 rem = 10px*/
}

body {
	font-family: "Open Sans", sans-serif;
	background: #f4f7fa;
	margin: 0;
	padding: 0;
	box-sizing: border-box;

}

li{
	list-style-type: none;
	
}

/*scroll bar*/

/* width */
::-webkit-scrollbar {
  width: 5px;

}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background:#a9b7d0; 
  border-radius: 10px;
}

